import styled from "@emotion/styled"

export const PageFill = styled.div`
  min-height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;

  &.dark {
    background: black;
    color: #dedede;
  }
`

export const Wrap = styled.div`
  margin: auto;
  padding-bottom: 20px;
`
